@use "global" as *;
.campaign {
  position: relative;
}

.campaign__inner {
  position: relative;
  text-align: center;
  margin-top: rem(36);
  margin: auto;
  @include mq(md) {
    margin: auto;
    margin-top: rem(131);
  
    padding-right: 0;
  }
}
.campaign__swiper {
  // padding-left: $padding-sp;
  @include mq(md) {
    display: flex;
    width: 100%;
    // height: 85%;
    margin: auto;
    margin-right: calc(50% - 50vw);
    padding-right: 0;
  }
}


.swiper__campaign {
  overflow: hidden;
  @include mq(md) {
    display: flex;
    height: 100%;
  }
}

.swiper-wrapper.campaign__slider {
  padding: rem(4) 0 0 0;
  max-width: rem(600);
  margin: auto;
  right: rem(32);
  @include mq(md) {
    display: flex;
    right: 0;
    top: 0;
    max-width: 100%;
    height: 100%;
    margin-top: rem(31);
    // max-width: none;
    // margin-left: rem(660);
    padding-bottom: rem(5);
  }
}


.button__campaign {
  text-align: center;
  // margin-right: rem(4);
  @include mq(md) {
    margin-top: rem(74);
  }
}

.card__title--campaign {
  display: flex;
  text-align: start;
  border-bottom: rem(1) solid $blue;
  font-family: $base-font-family;
  font-size: rem(12);
  margin: 0;
  padding-bottom: rem(6);
  letter-spacing: -0.18em;
  @include mq(md) {
    font-size: rem(12);
    letter-spacing: -0.18em;
    margin: 0;
    padding-bottom: rem(8);
  }
}
.card__text--slider {
  text-align: center;
  font-family: $base-font-family;
  font-weight: $normal;
  padding-top: rem(10);
  font-size: rem(14);
  @include mq(md) {
    padding-top: rem(14);
    padding-right: rem(14);
    font-size: rem(16);
  }
}

.card__campaign--prices {
  display: flex;
  justify-content: center;
  text-align: center;
  font-family: $third-font-family;
}

.card__campaign--price--price {
  font-family: $third-font-family;
  font-weight: $bold;
  text-decoration: line-through red rem(3);
  padding: rem(8) rem(16) 0 rem(2);
  font-size: rem(20);
  line-height: 2;
  @include mq(md) {
    font-family: $third-font-family;
    font-size: rem(24);
    padding: rem(12) rem(16) 0 rem(2);
    line-height: 1.8;
  }
}

.card__discount--discount {
  color: $blue;
  font-weight: $bold;
  font-family: $third-font-family;
  font-size: rem(32);
  line-height: 1;
  padding: rem(10) 0 0 rem(6);
  @include mq(md) {
    font-family: $third-font-family;
    font-size: rem(40);
    line-height: 1;
    padding: rem(8) 0 0 rem(12);
  }
}

.card__campaign--prices--night {
  display: flex;
  justify-content: center;
  margin: rem(0) rem(16) 0 rem(2);
  @include mq(md) {
    margin: rem(0) rem(24) 0 rem(2);
  }
}
