@use "global" as *;

.hero__contents {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 15;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $white;
  justify-content: center;
  @include mq(md) {
    z-index: 15;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}


.hero__title {
  font-size: rem(40);
  font-family: $third-font-family;
  font-weight: $regular;
  text-align: center;
  letter-spacing: 0.53em;
  text-indent: 0.53em;
  align-items: center;
  // margin-left: rem(22);
  @include mq(md) {
    font-weight: $normal;
    font-size: rem(40);
    margin-top: rem(85);
    // margin-left: rem(44);
    font-size: rem(80);
    letter-spacing: 0.53em;
    text-indent: 0.53em;
    line-height: 1;
  }
}

.hero__subtitle {
  display: block;
  font-size: rem(20);
  font-family: $second-font-family;
  font-weight: $regular;
  text-align: center;
  @include mq(md) {
    margin-top: rem(16);
    font-size: rem(20);
    font-family: $second-font-family;
    line-height: 1;
  }
}

.hero__img {
  height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  position: relative;
  object-fit: cover;
  // aspect-ratio: 375 / 667;
  @include mq(md) {
    object-fit: cover;
    height: 100vh;
    width: 100%;
    // aspect-ratio: 1440 / 768;
  }
}
.hero__img  {
  z-index: 10;
  object-fit: cover;
  // height: 100%;
  // width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  aspect-ratio: 375 / 667;
  object-fit: cover;
  @include mq(md) {
    object-fit: cover;
    // height: 100%;
    // width: 100%;
    aspect-ratio: 1440 / 768;
    object-fit: cover;
  }
}
